<template>
  <div id="loading" :class="MainClass"
    class="z-50 fixed bg-white select-none transition-opacity duration-500 ease-linear w-screen h-screen flex justify-center items-center">
    <div class="mb-8 flex flex-col justify-center items-center">
      <div class="w-16">
        <img v-if="!ErrorIF" src="@/assets/favicon.svg" alt="">
        <svg v-if="ErrorIF" t="1691358254210" class="w-16 h-16 text-red-900" viewBox="0 0 1024 1024" fill="currentColor" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="21520" width="200" height="200"><path d="M512 0C229.23 0 0 229.23 0 512s229.23 512 512 512 512-229.23 512-512S794.77 0 512 0z m169.71 568.57a80 80 0 0 1-113.14 113.14L512 625.14l-56.57 56.57a80 80 0 1 1-113.14-113.14L398.86 512l-56.57-56.57a80 80 0 1 1 113.14-113.14L512 398.86l56.57-56.57a80 80 0 1 1 113.14 113.14L625.14 512z" p-id="21521"></path></svg>
      </div>
      <div v-if="ErrorIF" class="font-semibold mt-3">Error Login</div>
    </div>
    <iframe hidden id="auth-iframe" src="https://seiya.mio.ski/post/token"></iframe>
  </div>
  <router-view v-if="MainLoad" />
</template>

<script setup>
import { ref, onMounted  } from "vue";
import axios from 'axios';
import { useRouter } from 'vue-router';

const router = useRouter();


const ErrorCount = ref(0);
const ErrorIF = ref(false);
const MainClass = ref("opacity-100");
const MainLoad = ref(false);

// Theme
// 检查本地存储中是否保存了用户选择的主题
const savedTheme = localStorage.getItem('theme');
// 如果有保存的主题，则将其应用到页面
if (savedTheme) {
  document.documentElement.classList.add(savedTheme);
} else {
  // 没有保存的主题，则检查浏览器主题设置
  const prefersDarkMode = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;

  // 根据浏览器主题设置应用相应的类名
  if (prefersDarkMode) {
    document.documentElement.classList.add('dark');
  } else {
    document.documentElement.classList.add('light');
  }
}
// End
onMounted(() => {
  if (location.pathname == "/") {
    JustGetToken();
  }else{
    getToken();
  }
  
})


const Load = () => {
  MainClass.value = "opacity-0";
  MainLoad.value = true;
  setTimeout(function () {
      const LoadValue = document.getElementById('loading')
      if (LoadValue) {
        LoadValue.remove()
      }
    }, 500);
}

const JustGetToken = () => {
  // const token = localStorage.getItem("UIMToken")
  // if (token) {Load();return;}
  window.addEventListener('message', function (event) {
    // 检查消息来源是否为可信任的域名
    if (event.origin != 'https://seiya.mio.ski') {
      Load()
      return;
    }

    // 处理接收到的消息，例如将用户token存储在本地
    const token = event.data;
    if (token == "No-UIMToken") {
      this.localStorage.getItem("UIMToken");
      Load()
    } else if (token == "Error-Origins") {
      this.localStorage.getItem("UIMToken");
      Load()
    } else {
      this.localStorage.setItem("UIMToken", token);
      check();
    }
})
};

function isChromeBasedBrowser() {
    var userAgent = navigator.userAgent;
    var isChrome = /Chrome\/\d+/.test(userAgent); // 匹配 Chrome 浏览器
    var isChromium = /Chromium\/\d+/.test(userAgent); // 匹配 Chromium 浏览器

    return isChrome || isChromium;
}


const getToken = () => {
  const token = localStorage.getItem("UIMToken")
  if (token){
    check()
    return
  }
  if (isChromeBasedBrowser()) {
    window.addEventListener('message', function (event) {
      // 检查消息来源是否为可信任的域名
      if (event.origin != 'https://seiya.mio.ski') {
        return;
      }
      // 处理接收到的消息，例如将用户token存储在本地
      const token = event.data;
      if (token == "No-UIMToken") {
        this.localStorage.removeItem("UIMToken");
        login()
      } else if (token == "Error-Origins") {
        this.localStorage.removeItem("UIMToken");
        UnGetToken()
      } else {
        this.localStorage.setItem("UIMToken", token);
        check();
      }
    });
  }else {
    UnGetToken()
  }
  function UnGetToken() {
    // 获取完整的查询字符串
    const queryString = window.location.search;

    // 创建 URLSearchParams 对象
    const params = new URLSearchParams(queryString);

    // 获取 target 参数的值
    const token = params.get('token');
    if (token) {
      if (token == "null") {
        router.replace(location.pathname);
        login()
      }else {
        localStorage.setItem("UIMToken", token);
        router.replace(location.pathname);
        check();
      }
    }else {
      location.replace("https://seiya.mio.ski/post/token?redirect_uri=" + location.href)
    }
  }
}

const check = () => {
  const token = localStorage.getItem("UIMToken");
  axios.get(
    "https://api.mioseiya.com/app/account/web/auth",
    {
      headers: {
        Authorization: `UIMAC ${token}`
      }
    }
  )
    .then(() => {
      Load()
    })
    .catch(() => {
      localStorage.removeItem("UIMToken")
      if (ErrorCount.value >= 2) {
        login()
      } else {
        ErrorCount.value++;
        getToken();
      }
    });
}

function login() {
  setTimeout(function () {
    location.href = "https://seiya.mio.ski/login?redirect_uri=" + location.href;
  }, 2000);
}

</script>

