import { createI18n } from 'vue-i18n'
let ilocale = null;

if (localStorage.getItem('lang') == null){
    localStorage.setItem('lang', 'local')
}
const lang = localStorage.getItem('lang');
if (lang == 'local'){
    ilocale = navigator.language;
}else {
    ilocale = lang;
}
const i18n = createI18n({
    legacy: false, // true or false
    locale: ilocale, // set locale
    fallbackLocale: 'en', // set fallback locale
    messages:  {
    zh : require("@/assets/locales/zh.json"),
    'zh-CN' : require("@/assets/locales/zh.json"),
    en : require("@/assets/locales/en.json"),
    } // set locale messages
})

export default i18n